import React, { useState, useEffect } from 'react';
import { Container, Typography, Grid, Box, Paper } from '@mui/material';
import StockMarket from './components/StockMarket';
import AITrader from './components/AITrader';
import StockChart from './components/StockChart';
import NewsFeed from './components/NewsFeed';
import PortfolioDetails from './components/PortfolioDetails';

function App() {
  const initialStocks = { 'Акция A': 10, 'Акция B': 15, 'Акция C': 20 }; // Стартовое количество акций для игроков
  const [userPortfolio, setUserPortfolio] = useState({
    balance: 1000,
    stocks: { ...initialStocks },
  });
  const [aiPortfolio, setAiPortfolio] = useState({
    balance: 1000,
    stocks: { ...initialStocks },
  });
  const [stocks, setStocks] = useState([
    { name: 'Акция A', price: 100, history: [100] },
    { name: 'Акция B', price: 150, history: [150] },
    { name: 'Акция C', price: 200, history: [200] },
  ]);
  const [news, setNews] = useState([]);

  const calculatePortfolioValue = (portfolio) => {
    let stockValue = Object.keys(portfolio.stocks).reduce((total, stockName) => {
      const stock = stocks.find(s => s.name === stockName);
      return total + (portfolio.stocks[stockName] * stock.price);
    }, 0);
    return portfolio.balance + stockValue;
  };

  const updateStockPrices = () => {
    setStocks(stocks.map(stock => {
      const newPrice = Math.max(stock.price + (Math.random() * 20 - 10), 0.0001); // Ограничение на минимальную цену
      return {
        ...stock,
        price: newPrice,
        history: [...stock.history, newPrice], // обновляем историю для графиков
      };
    }));
  };

  const generateNews = () => {
    const newsEvents = [
      { name: 'Акция A', impact: 'выросла', reason: 'Компании удалось заключить крупный контракт с государством.', effect: 1.15 },
      { name: 'Акция B', impact: 'упала', reason: 'Компания отчиталась о снижении прибыли в последнем квартале.', effect: 0.85 },
      { name: 'Акция C', impact: 'выросла', reason: 'Компания выпустила инновационный продукт, который вызвал интерес на рынке.', effect: 1.20 },
      { name: 'Акция A', impact: 'упала', reason: 'Ключевой лидер компании подал в отставку, вызвав неопределенность среди инвесторов.', effect: 0.80 },
      { name: 'Акция B', impact: 'выросла', reason: 'Регулятор дал одобрение на слияние с крупной международной корпорацией.', effect: 1.10 },
      { name: 'Акция C', impact: 'упала', reason: 'На производственных мощностях компании произошел пожар, что временно снизило объемы выпуска.', effect: 0.75 }
    ];

    if (Math.random() < 0.1) { // 10% вероятность события
      const event = newsEvents[Math.floor(Math.random() * newsEvents.length)];
      setStocks(stocks.map(stock => (
        stock.name === event.name 
          ? { ...stock, price: stock.price * event.effect, history: [...stock.history, stock.price * event.effect] }
          : stock
      )));
      setNews(prevNews => [`Новость: ${event.name} ${event.impact} в цене! Причина: ${event.reason}`, ...prevNews]);
    }
  };

  useEffect(() => {
    const interval = setInterval(() => {
      updateStockPrices();
      generateNews();
    }, 1000); // изменение каждые 1 секунду
    return () => clearInterval(interval);
  }, [stocks]);

  return (
    <Container>
      <Typography variant="h3" align="center" sx={{ marginY: 4 }}>Соревнование по торговле акциями</Typography>

      <Grid container spacing={4}>
        {/* График акций */}
        <Grid item xs={8}>
          <Paper elevation={3} sx={{ padding: 2, borderRadius: 2 }}>
            <StockChart stocks={stocks} />
          </Paper>

          {/* Покупка акций расположена строго под графиком */}
          <Box sx={{ marginTop: 2 }}>
            <Paper elevation={3} sx={{ padding: 2, borderRadius: 2 }}>
              <Typography variant="h6">
                Покупка акций:
              </Typography>
              <StockMarket 
                stocks={stocks} 
                portfolio={userPortfolio} 
                setPortfolio={setUserPortfolio} 
              />
            </Paper>
          </Box>
        </Grid>

        {/* Портфолио пользователя и ИИ расположены справа */}
        <Grid item xs={4}>
          <Grid container spacing={2}>
            <Grid item xs={12}>
              <Paper elevation={3} sx={{ padding: 2, borderRadius: 2 }}>
                <Typography variant="h6">
                  Портфель пользователя: Деньги на руках ${userPortfolio.balance.toFixed(2)}, 
                  Стоимость портфеля: ${calculatePortfolioValue(userPortfolio).toFixed(2)}
                </Typography>
                <PortfolioDetails portfolio={userPortfolio} />
              </Paper>
            </Grid>

            <Grid item xs={12}>
              <Paper elevation={3} sx={{ padding: 2, borderRadius: 2 }}>
                <Typography variant="h6">
                  Портфель ИИ: Деньги на руках ${aiPortfolio.balance.toFixed(2)}, 
                  Стоимость портфеля: ${calculatePortfolioValue(aiPortfolio).toFixed(2)}
                </Typography>
                <PortfolioDetails portfolio={aiPortfolio} />
              </Paper>
            </Grid>
          </Grid>
        </Grid>
      </Grid>

      {/* Добавляем компонент AITrader */}
      <AITrader 
        stocks={stocks} 
        portfolio={aiPortfolio} 
        setPortfolio={setAiPortfolio} 
      />

      {/* Новостная лента */}
      <Box sx={{ marginTop: 4 }}>
        <Paper elevation={3} sx={{ padding: 2, borderRadius: 2 }}>
          <NewsFeed news={news} />
        </Paper>
      </Box>
    </Container>
  );
}

export default App;
