import React from 'react';
import { Line } from 'react-chartjs-2';
import { Chart as ChartJS, CategoryScale, LinearScale, PointElement, LineElement, Title, Tooltip, Legend } from 'chart.js';

ChartJS.register(
  CategoryScale,
  LinearScale,
  PointElement,
  LineElement,
  Title,
  Tooltip,
  Legend
);

const StockChart = ({ stocks }) => {
  const colors = {
    'Акция A': 'rgba(255, 99, 132, 1)',
    'Акция B': 'rgba(54, 162, 235, 1)',
    'Акция C': 'rgba(75, 192, 192, 1)',
  };

  const data = {
    labels: stocks[0].history.map((_, index) => index + 1), // общий таймлайн
    datasets: stocks.map(stock => ({
      label: stock.name,
      data: stock.history,
      fill: false,
      borderColor: colors[stock.name], // фиксированные цвета для каждой акции
      tension: 0.1,
    })),
  };

  return (
    <div style={{ marginTop: 20 }}>
      <Line data={data} />
    </div>
  );
};

export default StockChart;