import React from 'react';
import { List, ListItem, ListItemText, Typography } from '@mui/material';

const PortfolioDetails = ({ portfolio }) => {
  return (
    <div>
      <Typography variant="h6" sx={{ marginTop: 2 }}>Подробное описание портфеля:</Typography>
      <List>
        {Object.keys(portfolio.stocks).map(stockName => (
          <ListItem key={stockName}>
            <ListItemText 
              primary={`${stockName}: ${portfolio.stocks[stockName]} акций`} 
            />
          </ListItem>
        ))}
      </List>
    </div>
  );
};

export default PortfolioDetails;