import React from 'react';
import { List, ListItem, ListItemText } from '@mui/material';

const NewsFeed = ({ news }) => {
  return (
    <List>
      {news.map((item, index) => (
        <ListItem key={index}>
          <ListItemText primary={item} />
        </ListItem>
      ))}
    </List>
  );
};

export default NewsFeed;