import React, { useEffect, useRef } from 'react';

const AITrader = ({ stocks, portfolio, setPortfolio }) => {
  const previousMAsRef = useRef({});

  useEffect(() => {
    const makeDecision = () => {
      stocks.forEach(stock => {
        const stockHeld = portfolio.stocks[stock.name] || 0;
        const currentPrice = stock.price;

        // Убедимся, что у нас достаточно данных истории
        if (stock.history.length >= 7) {
          // Параметры для скользящих средних
          const shortTermPeriod = 3;
          const longTermPeriod = 7;

          // Вычисляем краткосрочную и долгосрочную скользящие средние
          const shortTermMA =
            stock.history
              .slice(-shortTermPeriod)
              .reduce((sum, price) => sum + price, 0) / shortTermPeriod;
          const longTermMA =
            stock.history
              .slice(-longTermPeriod)
              .reduce((sum, price) => sum + price, 0) / longTermPeriod;

          // Получаем предыдущие скользящие средние
          const previousMAs = previousMAsRef.current[stock.name] || {};
          const previousShortTermMA = previousMAs.shortTermMA || shortTermMA;
          const previousLongTermMA = previousMAs.longTermMA || longTermMA;

          // Сигнал на покупку: краткосрочная пересекает долгосрочную снизу вверх
          if (
            previousShortTermMA <= previousLongTermMA &&
            shortTermMA > longTermMA &&
            portfolio.balance >= currentPrice
          ) {
            setPortfolio(prevPortfolio => ({
              ...prevPortfolio,
              balance: prevPortfolio.balance - currentPrice,
              stocks: {
                ...prevPortfolio.stocks,
                [stock.name]: (prevPortfolio.stocks[stock.name] || 0) + 1,
              },
            }));
          }

          // Сигнал на продажу: краткосрочная пересекает долгосрочную сверху вниз
          if (
            previousShortTermMA >= previousLongTermMA &&
            shortTermMA < longTermMA &&
            stockHeld > 0
          ) {
            setPortfolio(prevPortfolio => ({
              ...prevPortfolio,
              balance: prevPortfolio.balance + currentPrice,
              stocks: {
                ...prevPortfolio.stocks,
                [stock.name]: prevPortfolio.stocks[stock.name] - 1,
              },
            }));
          }

          // Обновляем предыдущие скользящие средние
          previousMAsRef.current[stock.name] = {
            shortTermMA,
            longTermMA,
          };
        }
      });
    };

    const interval = setInterval(makeDecision, 1000); // ИИ принимает решения каждые 5 секунд
    return () => clearInterval(interval);
  }, [stocks, portfolio, setPortfolio]);

  return null;
};

export default AITrader;