import React from 'react';
import { Card, CardContent, Typography, Button, Grid } from '@mui/material';

const StockMarket = ({ stocks, portfolio, setPortfolio }) => {
  const buyStock = (stock) => {
    if (portfolio.balance >= stock.price) {
      setPortfolio({
        ...portfolio,
        balance: portfolio.balance - stock.price,
        stocks: {
          ...portfolio.stocks,
          [stock.name]: portfolio.stocks[stock.name] + 1,
        },
      });
    }
  };

  const sellStock = (stock) => {
    if (portfolio.stocks[stock.name] > 0) {
      setPortfolio({
        ...portfolio,
        balance: portfolio.balance + stock.price,
        stocks: {
          ...portfolio.stocks,
          [stock.name]: portfolio.stocks[stock.name] - 1,
        },
      });
    }
  };

  return (
    <Grid container spacing={2}>
      {stocks.map((stock, index) => (
        <Grid item xs={12} key={index}>
          <Card>
            <CardContent>
              <Typography variant="h5">{stock.name}</Typography>
              <Typography variant="body2">Цена: ${stock.price.toFixed(2)}</Typography>
              <Button variant="contained" color="success" onClick={() => buyStock(stock)} sx={{ marginRight: 2 }}>
                Купить
              </Button>
              <Button variant="contained" color="error" onClick={() => sellStock(stock)}>
                Продать
              </Button>
            </CardContent>
          </Card>
        </Grid>
      ))}
    </Grid>
  );
};

export default StockMarket;